<template>
    <div>

        <zoomMeetingMobileSdk class="zoomVideoComponent" :meetingInfo="meetingInfo"
                              :forceOutActive="forceOutActive"
                              :hostEndActive="hostEndActive"
                              :userInfo="userInfo"
                              @zoomAction="zoomAction"
                              v-if="meetingInfoShow" role="0"></zoomMeetingMobileSdk>


        <div id="zoom-user-live-mobile">
            <userVideoComponent :meetingInfo="meetingInfo" v-if="meetingInfoShow" :joinStartActive="joinStartActive" @outActive="outActive"></userVideoComponent>
        </div>
        <iframe :src="testDebugUrl" id="testIframe" style="display:none" v-if="DEBUG && testDebugUrl"></iframe>
    </div>

</template>


<script>


    import AUTH from '@/config/web/auth'
    import userLiveHandler from '../../handler/userLiveHandler';
    import userLiveComuHandler from '../../handler/userLiveComuHandler';
    import userVideoComponent from "@/views/WEB/liveStream/userLive/component/userVideoComponent";
    import zoomMeetingMobileSdk from "@/views/WEB/liveStream/zoomWebMeeting/zoomMobileMeetingSdk";


    export default {
        name: 'userVideo',
        components: {
            zoomMeetingMobileSdk,
            userVideoComponent
        },
        data() {
            return {
                DEBUG: ((process.env.NODE_ENV == 'development') ? true : false),
                id: '',
                forceOutActive: false,
                hostEndActive: false,
                userInfo: {},
                joinStartActive:false,
                meetingInfo: {},
                meetingInfoShow: false,
                testDebugUrl:'',
            }
        },
        created() {

            this.id = this.$route.params.id;
            if (!this.$isMobile()) {
                location.replace('/liveStream/userVideoPc/' + this.id)
                return;
            }
            AUTH.setDevice(this.$isMobile());
            let member = AUTH.getSession();
            member = null;
            if (member && member?.memberInfo) {
                this.uid = member.memberInfo.uid;
                this.userName = member.memberInfo.name;
                this.userEmail = member.memberInfo.email;

            } else {

                this.uid = sessionStorage.getItem('joinUid');
                this.userName = sessionStorage.getItem('userName');
                this.userEmail = sessionStorage.getItem('userEmail');

            }
            this.userInfo = {
                uid: this.uid,
                userName: this.userName,
                userEmail: this.userEmail
            }

            if (this.uid && this.userName) {
                this.getMeetingInfo();

            } else {

            }

        },
        methods: {
            /**** 방송 시작 부분 ****/
            getMeetingInfo() {
                const params = {
                    id: this.id.toString(),
                    uid: this.uid,
                    userName: this.userName,
                    userEmail: this.userEmail
                }
                userLiveHandler.init(params, this, this.DEBGU);
                userLiveComuHandler.init(params, this);
                userLiveHandler.getLiveInfo((ret) => {

                    this.meetingInfo = ret;
                    this.meetingInfoShow = true;
                    if (this.DEBUG) {
                        this.testDebugUrl = 'http://localhost/testUserJsonRpc/' + this.id + '/' + this.uid + '/' + this.meetingInfo.info.uid;
                    }

                })
            },
            zoomAction(params) {
                switch (params.type) {
                    case 'start':
                        if (this.DEBUG) {
                            this.testDebugUrl = 'http://localhost/testUserJsonRpc/' + this.id + '/' + this.uid + '/' + this.meetingInfo.info.uid;
                        } else {
                            userLiveHandler.joinSocketRoom();
                        }
                        this.joinStartActive = true;
                        break;
                }
            },
            outActive() {
                this.forceOutActive = true;
            },

        }

    }
</script>


<style lang="scss">
    ul {
        padding: 0;
    }

    .sub-menu-box {
        padding: 10px;
        background: #fff;

        .sub-menu-header {
            display: flex;
            justify-content: space-between;
            padding: 0 0 10px 10px;

            .sub-menu-title {
                font-weight: bold;
                font-size: 11pt;

            }

            .icon {
                font-size: 12pt;
            }
        }

        .sub-box {
            padding: 0;

            li {
                padding: 7px 10px;
            }

        }
    }

    #zoom-user-live-mobile {

        width: calc(100vw);
        height: 100%;
        position: relative;
        overflow-y: auto;
        overflow-x: hidden;

        .clear {
            clear: both;
        }

        .header {
            width: 100%;
            padding: 0;
            maing: 0;
            border-bottom: solid 1px #C2C2C2;
            height: 60px;
            z-index: 100000;
            background: #fff;
            position: fixed;
            top: 0;
            left: 0;


            .subject-box {

                width: 75%;
                max-width: 75%;
                text-align: left;
                padding-left: 20px;
                line-height: 60px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                font-size: 10pt;
                height: 60px;
                color: #000;
                font-weight: bold;


            }


            .time {
                position: absolute;
                top: 0;
                right: 80px;
                text-align: right;
                font-size: 9pt;
                font-weight: bold;
                color: #000;
                line-height: 60px;

                .fa-clock {
                    color: #1E90FF;
                }
            }
            .end-button-mobile {
                position: absolute;
                top: 15px;
                right: 20px;
                width:50px;
                .end-button {
                    max-width:50px;
                    height: 30px;
                    line-height: 30px;
                    border-radius: 10px;
                    background:  #1E90FF;
                    color: #fff;
                }
            }
            #menu-response-box {
                display: none;
                position: absolute;
                top: 0;
                right: 80px;
                color: #000;
                line-height: 50px;
                text-align: right;
                background: #fff;
                z-index: 11100000;
                padding: 5px 10px 0 10px;

                .circle-box {
                    background: #999;
                    color: #fff;
                    width: 18px;
                    height: 18px;
                    display: inline-block;
                    text-align: center;
                    line-height: 16px;
                    border-radius: 9px;

                    .fa-square {
                        font-size: 8px;
                    }
                }
            }

        }

        #bottom-menu-box {
            width: 100%;
            position: fixed;
            left: 0;
            bottom: 0;
            height: 60px;
            z-index: 100001;
            border-top: solid 1px #C2C2C2;

            .menu-box {
                display: flex;
                justify-content: center;
                list-style-type: none;
                padding: 0;

                & > li {
                    text-align: center;
                    padding: 16px 15px 15px 15px;
                    font-weight: bold;
                    color: #000;
                    cursor: pointer;
                    position: relative;

                    img {
                        width: 30px;

                    }

                    .title-over {
                        display: none;
                        position: absolute;
                        z-index: 1000;
                        top: 49px;
                        left: 20px;
                        padding: 3px 10px;
                        min-width: 100px;
                        text-align: center;
                        background: #fafafa;
                        border: solid 1px #dcdcdc;
                        color: #000;
                        font-size: 9pt;

                        &.posi-top {
                            top: 7px;
                            left: 40px;
                        }
                    }

                    .is-dot {
                        display: inline-block;
                        position: absolute;
                        width: 10px;
                        height: 10px;
                        background: #ff0000;
                        border-radius: 5px;
                        right: 15px;
                        top: 15px;
                    }

                    .effect {
                        display: inline-block;
                        position: absolute;
                        width: 10px;
                        height: 10px;
                        background: #ff0000;
                        border-radius: 5px;
                        left: 15px;
                        top: 12px;
                    }

                    .sub-box {
                        display: none;
                        position: absolute;
                        top: 50px;
                        left: -40px;
                        min-width: 160px;
                        height: 80px;
                        -webkit-box-shadow: 1px 1px 5px -1px rgba(71, 70, 71, 0.53);
                        -moz-box-shadow: 1px 1px 5px -1px rgba(71, 70, 71, 0.53);
                        box-shadow: 1px 1px 5px -1px rgba(71, 70, 71, 0.53);
                        background: #fff;
                        border-radius: 5px;

                        &.webina {
                            max-height: 200px;
                            height: auto;
                            left: -25px;

                        }

                        &:hover {
                            display: block;
                        }

                        .sub-item {
                            font-size: 10pt;
                            font-weight: normal;
                            padding: 10px 0 10px 20px;
                            text-align: left;
                            color: #000;

                            &:hover {
                                background: #F4F4F4;
                            }

                        }
                    }


                }

            }
        }


        #screen-init {
            display: none;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 60px;
            z-index: 100001;
            background: rgba(255, 255, 255, 0.1);
        }

        .zoomVideoComponent {
            width: 100%;
            height: calc(100vh);
            max-height: calc(100vh);
        }

        .screenLayout {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: calc(100vh);
            z-index: 100001;
            background: rgba(0, 0, 0, 0.4);
            .name-box {
                width: calc(100vw - 40px);
                height: 350px;
                -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                border-radius: 10px;
                position: absolute;
                left: 20px;
                top: 100px;
                background: #ffffff;
                margin:0;
                padding:20px;
            }

            .screen-header {
                .del-box {
                    cursor: pointer;
                    position: absolute;
                    right: 15px;
                    top: 17px;
                    background: #999999;
                    width: 20px;
                    height: 20px;
                    text-align: center;
                    line-height: 20px;
                    border-radius: 10px;

                    i {
                        font-size: 10pt;
                        color: #fff;
                    }
                }
            }

            .summary {
                text-align: center;
                line-height: 150%;
                font-size: 11pt;
                color: #000;

            }

            .field-box {
                margin: 20px 0 0 0;

                .field {
                    width: 100%;
                }
            }

            .button-box {
                button {
                    margin-top:30px;
                    width: 100%;
                    height: 50px;
                    background: #007aff;
                    color: #fff;
                    border-radius: 25px;
                }
            }


        }

        .blackScreenLayout {
            position: fixed;
            display: none;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 100001;
            background: rgba(0, 0, 0, 0.3);
        }
        .exam-result-box {
            .content-box {
                text-align: center;

                .title-box {
                    color: #000;
                    font-weight: bold;
                    font-size: 14pt;
                }

                .desc-box {
                    color: #999;
                    font-size: 11pt;
                    margin: 30px 0;
                    line-height: 140%;
                }
            }

            .button-row {
                button {
                    width: 48%;
                    height: 50px;
                    border-radius: 25px;
                    background: #5E65E3;
                    color: #fff;

                    &:first-child {
                        margin-right: 4%;
                    }

                    &.cancle {
                        background: #999;
                    }
                }

                &.triple {
                    button {
                        width: 32%;

                        &:first-child {
                            margin-right: 2%;
                        }

                        &:last-child {
                            margin-left: 2%;
                        }
                    }
                }
            }

        }
    }
</style>
